<!-- 手机Loading -->
<template>
  <div class="loading-wrapper-phone">
    <div class="toast toast-loading">
      <img class="closeModal" @touchstart="closeModal" :src="close_icon" alt="关闭按钮">
      <img :src="qrcode" alt="qrcode" class="qrcode">
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        close_icon: require('../assets/img/guanbi.png'),
        qrcode: require('../assets/img/dimensionalcodepay.jpg')
      }
    },
    methods: {
      closeModal() {
        this.$emit('closeAntModal', 0);
      }
    }
  }
</script>
<style lang="less" scoped>
  .loading-wrapper-phone {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .5);
    .toast {
      left: 50%;
      top: 50%;
      transition-duration: .2s;
      z-index: 100;
      position: fixed;
      border-radius: 5px;
      transform: translate(-50%, -50%);
      background-color: #fff;
      width: 400px;
      height: 400px;
      &>.closeModal {
        position: absolute;
        top: 0;
        right: 0;
        width: 70px;
        height: 70px;
        z-index: 1;
      }
      &>.qrcode {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1.5);
        width: 200px;
      }
    }
  }
</style>


