<template>
  <div class="globalPadding">
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <wechatQRCode class="animated" v-if="qrcode" :qrcode="qrcode" v-on:closeModal="closeModal"></wechatQRCode>
    </transition>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <ant v-if="showAnt" class="animated" v-on:closeAntModal="closeAntModal"></ant>
    </transition>
    <div class="orderInfo">
      <div><span>金</span><span>额</span></div>
      <div><input type="number" v-model="payInfo_money"></div>
    </div>
    <div class="orderInfo">
      <div><span>姓</span><span>名</span></div>
      <div><input type="text" v-model="payInfo_userName"></div>
    </div>
    <div class="orderInfo">
      <div class="phoneNum">手机号码</div>
      <div><input type="tel" v-model="payInfo_phonenNum" placeholder="请输入您的手机号"></div>
    </div>
    <div class="orderInfo">
      <div><span>备</span><span>注</span></div>
      <div><input type="text" placeholder="如果还要其他说明请添加备注" v-model="payInfo_note"></div>
    </div>
    <section>
      <div v-for="(payItem, index) in payList" @click="changePayCode(payItem,index)" :key="index">
        <div><img :src="payItem.icon" :alt="payItem.way"><span>{{payItem.way}}</span></div>
        <div><span :class="{choosePayWay:1,bgPayWay:payItem.code}"></span></div>
      </div>
    </section>
    <footer>
      <button @click="rightWayEvent">立即支付</button>
    </footer>
    <div v-show="0" id="payment">
    </div>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  import wechatQRCode from '@/components/wechatQRCode'
  import ant from '@/components/antPre'
  export default {
    components: {
      headerNavBack,
      wechatQRCode,
      ant
    },
    data() {
      return {
        centerTitle: '快捷支付',
        payList: [{
            icon: require('../../assets/img/mayi.png'),
            way: '支付宝',
            type: 'alipay',
            code: 1
          },
          {
            icon: require('../../assets/img/wechat.png'),
            way: '微信支付',
            type: 'wechatPay',
            code: 0
          },
          {
            icon: require('../../assets/img/union_pay.png'),
            way: '银联',
            type: 'unionPay',
            code: 0
          },
          {
            icon: require('../../assets/img/ant_pay.png'),
            way: '蚂蚁分期',
            type: 'antPeriod',
            code: 0
          },
        ],
        payWay: '支付宝',
        payType: 'alipay',
        payInfo_money: '0.01',
        payInfo_userName: '拾光',
        payInfo_phonenNum: '17662030601',
        payInfo_note: 'PS功能精品课',
        aliPayURL: 'https://openapi.alipay.com/gateway.do',
        transfer: '',
        qrcode: '',
        showAnt: 0,
        form_html: ''
      }
    },
    mounted() {
      
    },
    methods: {
      // 关闭蚂蚁分期二维码Modal
      closeAntModal(info) {
        this.showAnt = info
      },
      // 关闭微信二维码支付Modal
      closeModal(info) {
        this.qrcode = info
      },
      // 立即支付
      rightWayEvent() {
        if (!this.payInfo_money) {
          this.$Toast({
            message: '请填写金额',
            position: 'middle',
            duration: 500
          })
          return
        }
        if (!this.payInfo_userName) {
          this.$Toast({
            message: '请填写姓名',
            position: 'middle',
            duration: 500
          })
          return
        }
        if (!this.payInfo_phonenNum) {
          this.$Toast({
            message: '请填写手机号',
            position: 'middle',
            duration: 500
          })
          return
        }
        if (!(/^1[3456789]\d{9}$/).test(this.payInfo_phonenNum)) {
          this.$Toast({
            message: '手机号码格式不正确',
            position: 'middle',
            duration: 500
          })
          return
        }
        if (!this.payInfo_note) {
          this.$Toast({
            message: '请填写备注',
            position: 'middle',
            duration: 500
          })
          return
        }
        switch (this.payType) {
          case 'alipay':
            this.axios.post(`/api/aliPay`, {
              bodys: this.payInfo_phonenNum,
              subject: this.payInfo_note,
              total_amount: this.payInfo_money
            }).then(data => {
              window.location.href = `${data.data.url}`
            }).catch(err => {
              console.log(err)
            })
            break
          case 'wechatPay':
            this.getWechatSign().then(data => {
              this.qrcode = `http://paysdk.weixin.qq.com/example/qrcode.php?data=${data.data.url.code_url}`
            })
            break
          case 'unionPay':
            this.getUnionPaySign().then(data => {
              let unionInfo = data.data.result.result
              this.form_html = data.data.html
              // 创建一个 form 
              var form1 = document.createElement("form")
              form1.id = "unionForm"
              form1.name = "unionForm"
              // 添加到 body 中 
              document.body.appendChild(form1)
              /**
               * @augments 生成form
               */
              var bizType = document.createElement("input")
              bizType.type = "text"
              bizType.name = 'bizType'
              bizType.value = unionInfo.bizType
              var txnSubType = document.createElement("input")
              txnSubType.type = "text"
              txnSubType.name = 'txnSubType'
              txnSubType.value = unionInfo.txnSubType
              var orderId = document.createElement("input")
              orderId.type = "text"
              orderId.name = 'orderId'
              orderId.value = unionInfo.orderId
              var backUrl = document.createElement("input")
              backUrl.type = "text"
              backUrl.name = 'backUrl'
              backUrl.value = unionInfo.backUrl;
              var signature = document.createElement("input")
              signature.type = "text"
              signature.name = 'signature'
              signature.value = unionInfo.signature;
              var txnType = document.createElement("input")
              txnType.type = "text"
              txnType.name = 'txnType'
              txnType.value = unionInfo.txnType
              var channelType = document.createElement("input")
              channelType.type = "text"
              channelType.name = 'channelType'
              channelType.value = unionInfo.channelType
              var frontUrl = document.createElement("input")
              frontUrl.type = "text"
              frontUrl.name = 'frontUrl'
              frontUrl.value = unionInfo.frontUrl
              var certId = document.createElement("input")
              certId.type = "text"
              certId.name = 'certId'
              certId.value = unionInfo.certId
              var version = document.createElement("input")
              version.type = "text"
              version.name = 'version';
              version.value = unionInfo.version
              var encoding = document.createElement("input")
              encoding.type = "text"
              encoding.name = 'encoding'
              encoding.value = unionInfo.encoding
              var accessType = document.createElement("input")
              accessType.type = "text"
              accessType.name = 'accessType'
              accessType.value = unionInfo.accessType
              var txnTime = document.createElement("input")
              txnTime.type = "text"
              txnTime.name = 'txnTime'
              txnTime.value = unionInfo.txnTime
              var merId = document.createElement("input")
              merId.type = "text"
              merId.name = 'merId'
              merId.value = unionInfo.merId;
              var payTimeout = document.createElement("input")
              payTimeout.type = "text"
              payTimeout.name = 'payTimeout'
              payTimeout.value = unionInfo.payTimeout
              var currencyCode = document.createElement("input")
              currencyCode.type = "text"
              currencyCode.name = 'currencyCode'
              currencyCode.value = unionInfo.currencyCode
              var signMethod = document.createElement("input")
              signMethod.type = "text"
              signMethod.name = 'signMethod'
              signMethod.value = unionInfo.signMethod
              var txnAmt = document.createElement("input")
              txnAmt.type = "text"
              txnAmt.name = 'txnAmt'
              txnAmt.value = unionInfo.txnAmt
              var riskRateInfo = document.createElement("input")
              riskRateInfo.type = "text"
              riskRateInfo.name = 'riskRateInfo'
              riskRateInfo.value = unionInfo.riskRateInfo
              // 将该输入框插入到 form 中 
              form1.appendChild(bizType)
              form1.appendChild(txnSubType)
              form1.appendChild(orderId)
              form1.appendChild(backUrl)
              form1.appendChild(signature)
              form1.appendChild(txnType)
              form1.appendChild(channelType)
              form1.appendChild(frontUrl)
              form1.appendChild(certId)
              form1.appendChild(encoding)
              form1.appendChild(version)
              form1.appendChild(accessType)
              form1.appendChild(txnTime)
              form1.appendChild(merId)
              form1.appendChild(payTimeout)
              form1.appendChild(currencyCode)
              form1.appendChild(signMethod)
              form1.appendChild(txnAmt)
              form1.appendChild(riskRateInfo);
              document.getElementById('payment').appendChild(form1)
              // form 的提交方式 
              form1.method = "POST"
              // form 提交路径 
              form1.action = unionInfo.action
              // 对该 form 执行提交 
              form1.submit()
            })
            break;
          case 'antPeriod':
            this.showAnt = 1
            break
        }
      },
      // 得到银联信息
      getUnionPaySign() {
        return new Promise((resovle, reject) => {
          this.axios.post('/api/unionPay').then(data => {
            resovle(data)
          }).catch(err => {
            reject(err)
          })
        })
      },
      // 得到微信信息
      getWechatSign() {
        return new Promise((resolve, reject) => {
          this.axios.post('/api/tenPayByWechatQRCode', {
            nickname: this.payInfo_userName,
            goodsPrice: this.payInfo_money,
            mobile: this.payInfo_phonenNum,
            remarks: this.payInfo_note,
          }).then(data => {
            resolve(data);
          }).catch(err => {
            console.log(err)
          });
        })
      },
      // 切换支付方式
      changePayCode(payWay, index) {
        this.payList.forEach((ele, ind) => {
          if (index == ind) {
            ele.code = 1
            this.payWay = ele.way
            this.payType = ele.type
          } else {
            ele.code = 0
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  // 订单信息
  .orderInfo {
    display: flex;
    background-color: #fff;
    .phoneNum {
      padding-left: 0.5em;
      color: #888;
      font-weight: 600;
      font-size: 30px;
    }
    &>div {
      text-align: center;
      display: inline-block;
      height: 90px;
      line-height: 90px;
      &>span {
        color: #888;
        font-weight: 600;
        font-size: 30px;
      }
      &>span:nth-child(1) {
        margin: 0 2em 0 1em;
      }
      &>input {
        border: none;
        border: 0;
        outline: none;
        height: 90px;
        width: 80%;
        text-indent: 1em;
        font-size: 30px;
        &::-webkit-input-placeholder {
          text-indent: 1em;
          font-size: 30px;
        }
      }
    }
    &>div:nth-child(1) {
      flex: 2;
    }
    &>div:nth-child(2) {
      flex: 8;
    }
  } // 选择方式布局
  section {
    margin-top: 20px;
    background-color: #fff;
    &>div {
      height: 100px;
      line-height: 100px;
      display: flex;
      &>div {
        display: inline-block;
        &>img {
          width: 70px;
          vertical-align: middle;
        }
        &>span {
          vertical-align: middle;
          font-size: 30px;
          margin-left: 1em;
        }
      }
      &>div:nth-child(1) {
        flex: 1;
        padding-left: 1em;
      }
      &>div:nth-child(2) {
        flex: 1;
        text-align: right;
        padding-right: 30px;
        &>.choosePayWay {
          display: inline-block;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          border: 3px solid #888;
        }
      }
    }
  }
  footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    text-align: center;
    background-color: #fff;
    height: 100px;
    line-height: 100px;
    &>button {
      color: #44b336;
      font-size: 40px;
      border: none;
      border: 0;
      outline: 0;
      background-color: transparent;
    }
  }
  .bgPayWay {
    background: url("../../assets/img/checked.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }
</style>




